@media only screen and (min-width: 450px) {
  .weui-tabbar {
    flex-direction: column;
    width: 60px;
    height: 100%;
  }
  .weui-tabbar:before {
    left: unset;
    height: unset;
    content: " ";
    position: absolute;
    right: 0;
    top: 0;
    width: 1px;
    bottom: 0;
    border-right: 1px solid var(--weui-FG-3);
    color: var(--weui-FG-3);
    transform-origin: 100% 0;
    transform: scaleX(0.5);
  }
  .weui-tabbar__item {
    flex: none;
  }
}
